import React from 'react'
import Styled from 'styled-components'
function LoginForm() {

    return (
        <Wrapper>
            <Header>
                Login to Net Gallery
            </Header>
            <Body>
                <Input type="text" />
            </Body>
        </Wrapper>
    )
}

export default LoginForm

const Wrapper = Styled.div`
    display: block;
    margin: 2vh;
    margin-left: auto;
    margin-right: auto;
    max-width: 600px;
    text-align: center;
    border: 1px solid #69697d;
`

const Header = Styled.div`
    display: block;
    background: #20242A;
    padding: 1vh;
`
const Body = Styled.div`
    display: block;
    background: #353B45;
    padding: 5px;
`
const Input = Styled.input`
    border: 1px #69697d solid;
    border-radius: 7px;
    min-height: 30px;
    background-color: #2D323B;
`